import React from "react"
import { MDXProvider } from "@mdx-js/react"
import { Link } from "gatsby-theme-material-ui"
import Typography from "@material-ui/core/Typography"
import List from "@material-ui/core/List"
import ListItem from "@material-ui/core/ListItem"
import ListItemText from "@material-ui/core/ListItemText"
import Paper from "@material-ui/core/Paper"
import Divider from "@material-ui/core/Divider"
import TableContainer from "@material-ui/core/TableContainer"
import Table from "@material-ui/core/Table"
import TableHead from "@material-ui/core/TableHead"
import TableBody from "@material-ui/core/TableBody"
import TableRow from "@material-ui/core/TableRow"
import TableCell from "@material-ui/core/TableCell"
import BlockQuote from "./components/BlockQuote"
import CodeBlock from "./components/CodeBlock"

const components = {
  h1: props => <Typography {...props} variant="h1" component="h1" />,
  h2: props => <Typography {...props} variant="h2" component="h2" />,
  h3: props => <Typography {...props} variant="h3" component="h3" />,
  h4: props => <Typography {...props} variant="h4" component="h4" />,
  h5: props => <Typography {...props} variant="h5" component="h5" />,
  h6: props => <Typography {...props} variant="h6" component="h6" />,
  p: props => <Typography {...props} variant="body1" component="p" />,
  blockquote: props => <BlockQuote square {...props} />,
  thematicBreak: Divider,
  hr: Divider,
  ul: props => (
    <Paper square>
      <List {...props} component="ul" />
    </Paper>
  ),
  ol: props => (
    <Paper square>
      <List {...props} component="ol" />
    </Paper>
  ),
  li: props => (
    <ListItem>
      <ListItemText {...props} />
    </ListItem>
  ),
  table: props => (
    <TableContainer component={Paper} square>
      <Table {...props} />
    </TableContainer>
  ),
  thead: TableHead,
  tbody: TableBody,
  tr: TableRow,
  th: props => <TableCell {...props} variant="head" component="th" />,
  td: TableCell,
  a: Link,
  pre: props => <Paper {...props} square />,
  code: CodeBlock,
}

export default function WrapWithMDXProvider({ element }) {
  return <MDXProvider components={components}>{element}</MDXProvider>
}
